import React  from "react";
import lays from "../lays.png"; 
import {Link} from "react-router-dom";

class Chips extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            count : 0 , 

        }
        this.addBags = this.addBags.bind(this);
    }

    addBags(){
        var random = Math.floor(Math.random()*600); 
        var random2 = Math.floor(Math.random()*1000); 
        var newImg = document.createElement("img");
        newImg.setAttribute("src" , lays) ;
        newImg.setAttribute("style" , `
        position: absolute;
        height: 150px;
        width: 200px;
        top: ${random}px;
        left:${random2}px;
        right:${random2}px;
        bottom:${random}px;
        z-index:1 ;
        `);
        document.getElementById("con").appendChild(newImg); 
        

        var countBag= this.state.count ; 
        countBag++ ; 

        this.setState({
            count:countBag , 
        })
    }

    render(){
        return(
            <React.Fragment>
                <div className="container-fluid chips-container" id="con">
                    <div className="row">
                        <div className="col p-5">
                            <div className="sardines-button-container">
                                <h2>Bags eaten: {this.state.count}</h2>
                                <button className="btn btn-warning" onClick={this.addBags}>NOM NOM NOM</button>
                                <Link to="/" className="sardines-gohome">Go Home</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>

        )
    }
}



export default Chips ; 